export interface SliderState {
  data: Array<{
    en: string;
    ru: string;
    partOfSpeech: partOfSpeechType;
    img?: string;
  }>;
  isEnglish: boolean;
  moveAside: number;
}

export enum SliderActionTypes {
  MOVE_SLIDER_LEFT = "MOVE_SLIDER_LEFT",
  MOVE_SLIDER_RIGHT = "MOVE_SLIDER_RIGHT",
  CHANGE_LANGUAGE = "CHANGE_LANGUAGE",
}

interface MoveSliderLeftAction {
  type: SliderActionTypes.MOVE_SLIDER_LEFT;
}
interface MoveSliderRightAction {
  type: SliderActionTypes.MOVE_SLIDER_RIGHT;
}
interface ChangeLanguageAction {
  type: SliderActionTypes.CHANGE_LANGUAGE;
}

export type SliderAction =
  | MoveSliderLeftAction
  | MoveSliderRightAction
  | ChangeLanguageAction;

export type partOfSpeechType =
  | "verb"
  | "noun"
  | "adjective"
  | "pronoun"
  | "numerals"
  | "adverb";
