import {
  SliderAction,
  SliderActionTypes,
  SliderState,
} from "../../types/slider";

const initialState: SliderState = {
  data: [
    {
      en: "Apple",
      ru: "Яблоко",
      partOfSpeech: "noun",
      img: "https://st.depositphotos.com/1036708/1328/i/600/depositphotos_13288517-stock-photo-apple-with-leaf.jpg",
    },
    {
      en: "Grape",
      ru: "Виноград",
      partOfSpeech: "noun",
      img: "https://calorizator.ru/sites/default/files/imagecache/product_512/product/grape-3.jpg",
    },
    { en: "Application", ru: "Приложение", partOfSpeech: "noun" },
    {
      en: "Perhaps",
      ru: "Возможно",
      partOfSpeech: "adverb",
      img: "https://i.ytimg.com/vi/ggz2IbcBD70/mqdefault.jpg",
    },
    { en: "Competitor", ru: "Конкурент", partOfSpeech: "noun" },
    { en: "Sweat", ru: "Потеть", partOfSpeech: "verb" },
    { en: "Gulp", ru: "Глоток", partOfSpeech: "noun" },
    {
      en: "Throw",
      ru: "Бросать",
      partOfSpeech: "verb",
      img: "https://cf2.ppt-online.org/files2/slide/v/vgQKiX2shkEZ9Djwbf7J8YqABeIRdS1uTzy4an/slide-16.jpg",
    },
  ],
  isEnglish: true,
  moveAside: 0,
};

export const sliderReducer = (
  state = initialState,
  action: SliderAction
): SliderState => {
  switch (action.type) {
    case SliderActionTypes.MOVE_SLIDER_LEFT: {
      return {
        ...state,
        moveAside: state.moveAside + 100,
      };
    }
    case SliderActionTypes.MOVE_SLIDER_RIGHT: {
      return {
        ...state,
        moveAside: state.moveAside - 100,
      };
    }
    case SliderActionTypes.CHANGE_LANGUAGE: {
      return {
        ...state,
        isEnglish: !state.isEnglish,
      };
    }
    default:
      return state;
  }
};
