import { SliderAction } from "../../types/slider";
import { SliderActionTypes } from "../../types/slider";

export function changeLanguage(): SliderAction {
  return { type: SliderActionTypes.CHANGE_LANGUAGE };
}

export function moveSliderLeft(): SliderAction {
  return { type: SliderActionTypes.MOVE_SLIDER_LEFT };
}

export function moveSliderRight(): SliderAction {
  return { type: SliderActionTypes.MOVE_SLIDER_RIGHT };
}
